.style-layouts{float:left;width:80%;height:400px;overflow-y:auto}.style-layouts dl:hover{background-color:#7c3;padding-left:10px;border-radius:4px}.layout-preview{float:left;width:20%;max-width:400px}.layout-preview img{max-width:100%}.layout-preview .larger-view{position:absolute;visibility:hidden;left:-2000px;top:-100px;border:5px solid #666;border-radius:5px;z-index:2000;max-width:80%;opacity:0;-webkit-transition:visibility 0s linear .5s,opacity .5s linear;transition:visibility 0s linear .5s,opacity .5s linear}.layout-preview:hover .larger-view{visibility:visible;opacity:1;-webkit-transition-delay:0s;transition-delay:0s;left:0}